#root {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
}

.logo {
  height: 6em;
  padding: 1.5em;
  will-change: filter;
  transition: filter 300ms;
}

.logo:hover {
  filter: drop-shadow(0 0 2em #646cffaa);
}

.logo.react:hover {
  filter: drop-shadow(0 0 2em #61dafbaa);
}

@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  a:nth-of-type(2) .logo {
    animation: logo-spin infinite 20s linear;
  }
}

.card {
  padding: 2em;
  width: 100%;  /* Aggiungi una larghezza del 100% per adattarsi alla larghezza del dispositivo */
}

.read-the-docs {
  color: #888;
}

/* Media Queries per dispositivi con larghezza massima di 768px (tablet e smartphone) */
@media (max-width: 768px) {
  body {
    font-size: 16px; /* Riduci la dimensione del testo */
  }

  .container {
    padding: 10px;
  }

  .navbar {
    display: block;
    text-align: center;
  }

  .navbar a {
    display: block;  /* I link vengono disposti su righe separate */
    padding: 12px 16px; /* Maggiore spazio tra i link */
    font-size: 1.2rem;  /* Aumenta la leggibilità dei link */
  }

  .box {
    padding: 15px;
    margin: 10px 0;
  }

  .logo {
    height: 5em;  /* Riduci la dimensione della logo su schermi piccoli */
  }

  .card {
    padding: 1em;  /* Riduci il padding su schermi più piccoli */
  }

  .read-the-docs {
    font-size: 14px;
  }
}

/* Media Queries per dispositivi con larghezza massima di 480px (smartphone piccoli) */
@media (max-width: 480px) {
  .navbar {
    font-size: 14px; /* Riduci ulteriormente la dimensione del testo */
  }

  .container {
    padding: 5px;
  }

  .box {
    padding: 10px;
    margin: 5px 0;
  }

  .logo {
    height: 8em;  /* Riduci ulteriormente la dimensione della logo */
  }
}
.outlinedTitle {
  font-size: 48px;
  color: transparent;
  -webkit-text-stroke: 2px white;
  text-stroke: 2px white;
  font-weight: bold;
  margin-left: 16px;
}
